<h1 id="android-scheduling" tabindex="-1"><a class="header-anchor" href="#android-scheduling" aria-hidden="true">#</a> Android Scheduling</h1>
<p>Sometimes you want to to execute code at a certain time, i.e to schedule the execution of it. Seems like a straight forward thing to do, right? Well, in an attempt to preserve battery, Google have made this a bit more complicated than you initially think it would be.</p>
